export const coments = [{id:'0', rating:3.5, country:'India', comment:`India is a land of many cultures.You can always find jolly people around.The geographical extent of India will give you an opportunity to most of the climatic conditions under one roof.You can prefer staying in lodges as they are very economical and you can always find a place for you to push your head in.`, name:'Armon Varges', ocupation:'/writer', image:male1},{id:'1', rating:4.5, country:'Kenya', comment:`Kenya should be the worlds finest safari destination. It has every natural advantage. Wrapped around the equator, it has the most extraordinary range of landscape, from the Indian Ocean to the Turkana Desert, the Great Rift Valley to the forests of Mt Elgon and the soaring highlands of Mt Kenya.`, name:'Mia Karlowitz', ocupation:'/attorney',image:female1},{id:'2', rating:5, country:'France', comment:`I totally recommend it. It was excellent
I took a package of 3 activities: Dinner in Pocople, Night Bus Tour and Moulin Rouge. I was a bit concerned by the bad reviews I read here and the cheap price of the package, but I have to say everything was good. We didnt have any problem at all and we enjoyed a great offer, for the money of 1 activity we did 3! Super recommended.`, name:'Robert Doeng', ocupation:'/sport journalist', image:male2},{id:'3', rating:5, country:'Italy', comment:`We were in Venice, Florence and Rome. Each of had been many times before. So the new cities were the most impressive. Lucca and Ostia Antica and Montepulciano. Andrea and one of the guides recommended an excellent restaurant in Lucca, *** in the basement of a castle. The food and atmosphere unexpected. Also in Lucca we managed to get tickets for a Puccini concert which was magnificent.`, name:'Susan Jared', ocupation:'/doctor', image:female2}]